import { Component } from '@angular/core';
import { AlertController,IonicPage,ModalController, NavController, NavParams , InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../providers';
import { TranslateService } from '@ngx-translate/core';

@IonicPage()
@Component({
  selector: 'page-suscriptor-list',
  templateUrl: 'suscriptor-list.html',
})
export class SuscriptorListPage {
  public loading: boolean = true;
  items: any=[];
  items2: any=[];
  v1:any;
  v2:any;
  v3:any;
  v4:any;
  idempresa: any;
  idafiliado:any='na';

  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';

  constructor( public alertCtrl: AlertController,public msj: NotificationProvider, public translateService: TranslateService  ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth) {
      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value
       
    })
  }
  
  public clearSearchg(): void {
    this.gSearchQuery = '';
  }
  
      

  pideregistros() { 
      let self = this;
      self.loading = true;
      this.regService.getReg('todos','suscriptors').then((data) => {
 
      this.items=data;
      self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
  });
  }
  ionViewDidLoad() {
    this.pideregistros();

    this.translateService.get(["CANCEL",
    "INGRESAR",
    "BUSCARPOR",
    "BUSCAR"
  ]).subscribe(
    (values) => {
     this.v1=values.CANCEL
     this.v2=values.INGRESAR
     this.v3=values.BUSCARPOR
     this.v4=values.BUSCAR
    });

  }
  
  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }

  comprasaldo(item : any) : void
  {
    this.navCtrl.push('ComprasaldoListPage', { record : item });
  }
  deleteRecord(item : any, no) : void
  {
    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operaciÃ³n?',
    buttons: [
      {text: 'Si',
        handler: () => {
              
          let recordID 		: string		= item.nodpi;
          this.regService.deleteReg(recordID+ '/' + this.idempresa + '/' + this.idafiliado,'suscriptors').then((data) => {
              this.items.splice(no, 1);
              this.items = this.items.concat([]);
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
      
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();

   
  
  }
  value:any;

  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('SuscriptorCreatePage', { record : item },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items.splice(no, 1);
        this.items = this.items.concat([data]);
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
   addRecord() : void
   {
    let addModal = this._MODAL .create('SuscriptorCreatePage',{ record : null },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items = this.items.concat([data]);
      }  
    })
    addModal.present();
      //this.navCtrl.push('EmpresaCreatePage');
   }
   viewRecord(item : any): void
   {
     // let modal = this._MODAL.create(ViewGalleryPage, { record : item });
     // modal.present();
   }

   excludeTracks: any = [];

   presentFilter() {
     /*
    let modal = this._MODAL.create(ScheduleFilterPage, this.excludeTracks);
    modal.present();

    modal.onWillDismiss((data: any[]) => {
      if (data) {
        this.excludeTracks = data;
        this.updateSchedule();
      }
    });
*/
  }

  showFilter()     
  {

    let prompt = this.alertCtrl.create({
      title: this.v3+' DPI/CUI',
      message: this.v2+' DPI/CUI',
      inputs: [
        {
          name: 'dpi'
        }
      ],
      buttons: [
        {
          text: this.v1
        },
        {
          text: this.v4,
          handler: todo => {
 
                if(todo){
 
                  this.regService.getReg(todo.dpi,'suscriptors').then((data) => {
 
                    this.items=data;
                  
                }, (err) => {
                  this.msj.showError3('Error',err.error);
                });
                   console.log(todo);
 
                }
 
 
          }
        }
      ]
    });
 
    prompt.present();

  }


  updateSchedule() {
    // Close any open sliding items when the schedule updates
   /*
    this.scheduleList && this.scheduleList.closeSlidingItems();

    this.confData.getTimeline(this.dayIndex, this.queryText, this.excludeTracks, this.segment).subscribe((data: any) => {
      this.shownSessions = data.shownSessions;
      this.groups = data.groups;
    });
    */
  }


}



